<template>
    <div class="systemLog">
        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple-dark typeMain">
                    <p style="padding-bottom: 20px">系统日志</p>
                        <el-table
                        :data="tableData"
                        style="width: 100%;border: 1px solid #eee;border-bottom: none;">
                        <el-table-column
                            prop="optDate"
                            label="日志"
                            width="500">
                        </el-table-column>
                        <el-table-column
                            prop="methodRemark"
                            label="说明">
                        </el-table-column>
                    </el-table>
                </div>

                <div class="fy">
                    <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        background
                        layout="prev, pager, next"
                        :total="count">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data() {
        return {
            currentPage: 1, //初始页
            pagesize:10,    //    每页的数据
            count: 0,
            tableData: [],
            page: 0
        }
    },
    methods: {
        getLogs(pageNum) {
            api.getLog(10, pageNum).then( res => {
                console.log(res)
                this.tableData = res.data.list;
                this.count = res.data.count;
            })
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
            console.log(this.currentPage)  //点击第几页
            this.getLogs(this.currentPage - 1)
        }
    },
    created() {
        this.getLogs(this.page)
    },
}
</script>

<style lang="stylus" scoped>
.systemLog 
    border-radius 10px
    overflow hidden
    background #fff
    padding 20px
.fy
    display flex
    justify-content center
    padding 20px 0 0 0
</style>



